import styled, { css, keyframes } from 'styled-components'
import * as Icon from 'react-feather'

const media = {
    mobile: (...args) => css`
        @media (max-width: 400px) {
            ${css(...args)};
        }
    `,
}

export const Landing = styled.section`
    height: 50em;
    background-color: #1f41f2;

    ${media.mobile`
      height: 35em;
    `};
`

export const LandingContent = styled.div`
    padding-top: 10em;
`

export const Heading = styled.h1`
    color: white;
    font-size: 3em;
    text-align: center;
    letter-spacing: 0.15em;
    width: 50%;
    margin: auto;
    font-weight: 600;
    text-align: left;
    line-height: 1.8em;
    font-style: italic;

    ${media.mobile`
      padding-top: 6em; 
      font-size: 1.4em;
      width: 70%;
    `};
`

export const LetsWorkLink = styled(Heading)`
    text-decoration: underline;
    font-size: 1.5em;
    font-weight: 300;
    padding-top: 0.5em;

    ${media.mobile`
    font-size: 1.4em
      padding-top: 0.5em;
    `};
`

export const Work = styled.div`
    height: 35em;
    background-color: #1f41f2;
`

export const Brief = styled.div`
    background: #ffd401;
    height: 17em;
    weight: 100%;
`

export const BriefText = styled.div`
    font-weight: 500;
    font-size: 20px;
    text-align: center;
    letter-spacing: 0.08em;
    line-height: 1.4em;
    color: white;
    width: 55%;
    margin: auto;
    padding-top: 2em;
`

export const Approach = styled(BriefText)`
    padding-top: 1em;
    text-decoration-line: underline;
    font-size: 1em;
`



